/**
 * 
 * @author 
 * @property {Function} getInfo Id 
 * @property {Function} getTrees  
 * @property {Function} addInfo 
 * @property {Function} updateInfo 
 * @property {Function} deleteInfo Id,  
 */
import web from '@/common/variable/website.js'; // 
import publicF from '@/common/function/publicfunc.js'; // 
import changeobjF from '@/common/function/changeobjectecordname.js'; // 
import filterobjF from '@/common/function/filterobject.js'; // 
import login from '@/common/storage/loginstorage.js'; // 

module.exports = {
	/** Id
	 * @param {Object} obj
	 * @property {Number} id Id
	 * @property {Function} before null
	 * @property {Function} success null
	 * @property {Function} fail null
	 * @property {Function} complete null
	 */
	getInfo: function(obj) {
		// 
		let id ;	// id
		let before;	// 
		let success;	// 
		let fail ;		// 
		let complete ;	// 
		if(Boolean(obj)){
			id = obj.id;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if (typeof(before) === "function") {
			before();
		}
		uni.request({
			url: web.website + '/api/SvcOrganization/Info/' + id,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success: function(result) {
				if (result.statusCode === 200) {
					if (result.data.ResultCode === "0") {
						if (typeof(success) === "function") {
							success(result.data.Data ,result.data.ResultMsg);
						}
						return;
					}
				}
				if (typeof(fail) === "function") {
					fail(result);
				}
			},
			fail: function(errors) {
				if (typeof(fail) === "function") {
					fail(errors);
				}
			},
			complete: function() {
				if (typeof(complete) === "function") {
					complete();
				}
			}
		});
	},
	/** 
	 * @param {Object} obj 
	 * @property {Object} data 
	 * @property {Boolean} isformat true
	 * @property {Boolean} isFilter false
	 * @property {Number} orgId Id(isFiltertrue) 
	 * @param {Function} before null
	 * @param {Function} success null
	 * @param {Function} fail null
	 * @param {Function} complete null
	 */
	getTrees: function(obj) {
		// 
		let data; // 
		let isformat = true; // 
		let isFilter = false; // 
		let orgId; 			// Id
		let before ; // 
		let success ; // 
		let fail ; // 
		let complete; // 
		if (Boolean(obj)) {
			data = obj.data;
			isformat = (obj.isformat != false);
			isFilter = Boolean(obj.isFilter);
			orgId = obj.orgId;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		let params = publicF.urlEncode(data);
		if (typeof(before) === "function") {
			before();
		}
		uni.request({
			url: web.website + '/api/SvcOrganization/GetFullOrg?' + params,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success: function(result) {
				if (result.statusCode === 200) {
					if (result.data.ResultCode === "0" && Boolean(result.data.Data)) {
						if (typeof(success) === "function") {
							var data = result.data.Data;
							// 
							var fullorgTree = isformat ? changeobjF.change(data.Children,"Value", "Name","Children") : data;
							// 
							var organtrees = isFilter? filterobjF.filterbyvalue_list(fullorgTree, orgId) : fullorgTree;
							success(fullorgTree , organtrees);
						}
						return;
					}
				}
				if (typeof(fail) === "function") {
					fail(result);
				}
			},
			fail: function(errors) {
				if (typeof(fail) === "function") {
					fail(errors);
				}
			},
			complete: function() {
				if (typeof(complete) === "function") {
					complete();
				}
			}
		})
	},
	/** 
	 * @param {Object} obj 
	 * @property  {Object} data 
	 * @property {Function} before null
	 * @property {Function} success null
	 * @property {Function} fail null
	 * @property {Function} complete null
	 */
	addInfo: function(obj) {
		// 
		let data;		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if (typeof(before) === "function") {
			before();
		}
		uni.request({
			url: web.website + '/api/SvcOrganization/Add',
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			data: data,
			method: 'POST',
			success: function(result) {
				if (result.statusCode === 200) {
					if (result.data.ResultCode === "0") {
						if (typeof(success) === "function") {
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if (typeof(fail) === "function") {
					fail(result);
				}
			},
			fail: function(errors) {
				if (typeof(fail) === "function") {
					fail(errors);
				}
			},
			complete: function() {
				if (typeof(complete) === "function") {
					complete();
				}
			}
		});
	},
	/** 
	* @param {Object} obj
	* @property  {Object} data 
	* @property {Function} before null
	* @property {Function} success null
	* @property {Function} fail null
	* @property {Function} complete null
	 */
	updateInfo: function(obj) {
		// 
		let data;		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if (typeof(before) === "function") {
			before();
		}
		uni.request({
			url: web.website + '/api/SvcOrganization/Update',
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			data: data,
			method: 'POST',
			success: function(result) {
				if (result.statusCode === 200) {
					if (result.data.ResultCode === "0") {
						if (typeof(success) === "function") {
							success(result.data.Data ,result.data.ResultMsg);
						}
						return;
					}
				}
				if (typeof(fail) === "function") {
					fail(result);
				}
			},
			fail: function(erros) {
				if (typeof(fail) === "function") {
					fail();
				}
			},
			complete: function() {
				if (typeof(complete) === "function") {
					complete();
				}
			}
		});
	},
	/** id,
	 * @param {Object} obj
	 * @property {Function} before 
	 * @property {Function} success 
	 * @property {Function} fail 
	 * @property {Function} complete 
	 */
	deleteInfo: function(obj) {
		let id ; 		// id
		let before; 	// 
		let success ; 	// 
		let fail ; 		// 
		let complete;	// 
		if (Boolean(obj)) {
			id = obj.id;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		if (typeof(before) === "function") {
			before();
		}
		uni.request({
			url: web.website + '/api/SvcOrganization/Delete/' + id,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'POST',
			success: function(result) {
				uni.hideLoading();
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "0" && result.data.Data) {
						if (typeof(success) === "function") {
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if (typeof(fail) === "function") {
					fail(result);
				}
			},
			fail: function(errors) {
				if (typeof(fail) === "function") {
					fail(errors);
				}
			},
			complete: function() {
				if (typeof(complete) === "function") {
					complete();
				}
			}
		});
	},
	// ===============================================================================================================================
}
