/**
 * 
 * @author 
 * @property {Function} getTrees -
 * @property {Function} getTreesFormat ()
 * @property {Function} deleteInfo id
 * @property {Function} getCustomerTree 
 */
import web from '@/common/variable/website.js';			// 
import publicF from '@/common/function/publicfunc.js';		// 
import changeobjF from '@/common/function/changeobjectecordname.js';	// 
import login from '@/common/storage/loginstorage.js';	// 
module.exports = {
	/** -
	 * @param {Object} obj
	 * 	@property {Object} data 
	 *  @property {Function} before  
	 *  @property {Function} success  
	 *  @property {Function} fail  
	 *  @property {Function} complete  
	 */
	getTrees : function(obj){
		let mchId;			// Id
		let before ;		// 
		let success;		// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			mchId = Boolean(obj.mchId) ? obj.mchId : 0;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		if(typeof(before) === "function" ){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcCategory/Tree?mchId=' + mchId,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success:function(result){
				if(result.statusCode === 200){
					if(result.data.ResultCode === "0"){
						if(typeof(success) === "function" ){
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function" ){
					fail(result);
				}
			},
			fail:function(errors){
				if( typeof(fail) === "function" ){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		})
	},
	/** ()
	 * @param {Object} obj
	 * 	@property {Object} data 
	 *  @property {Function} before  
	 *  @property {Function} success  
	 *  @property {Function} fail  
	 *  @property {Function} complete  
	 */
	getTreesFormat : function(obj){
		// 
		let data;		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		var params = publicF.urlEncode(data);
		if(typeof(before) === "function" ){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcCategory/Tree?' + params ,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success:function(result){
				if(result.statusCode === 200){
					if(result.data.ResultCode === "0"){
						if(typeof(success) === "function" ){
							success(changeobjF.change(result.data.Data, "CategoryId", "Name", "Children") ,result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function" ){
					fail(result);
				}
			},
			fail:function(errors){
				if( typeof(fail) === "function" ){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		})
	},
	/** id
	 * @param {Object} obj
	 * 	@property {Number} id Id
	 *  @property {Function} before  
	 *  @property {Function} success  
	 *  @property {Function} fail  
	 *  @property {Function} complete  
	 */
	deleteInfo: function(obj) {
		// 
		let  id;		// id
		let before;		// 
		let success ;	// 
		let fail ;		// 
		let complete ;	// 
		if(Boolean(obj)){
			id = Boolean(obj.id)? obj.id : 0 ;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcCategory/Delete/' + id,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'POST',
			success: function(result) {
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "0") {
						if(typeof(success) === "function"){
							success(result.data.Data, result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function"){
					fail(result);
				}
			},
			fail: function(erros) {
				if(typeof(fail) === "function"){
					fail(erros);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		})
	},
	// ===================================  =============================================================
	/** -
	 * @param {Object} obj
	 * 	@property {Number} mchId id
	 *  @property {Function} before  
	 *  @property {Function} success  
	 *  @property {Function} fail  
	 *  @property {Function} complete  
	 */
	getCustomerTree : function(obj){
		let mchId		// Id
		let before;		// 
		let success ;	// 
		let fail ;		// 
		let complete ;	// 
		if(Boolean(obj)){
			mchId = Boolean(obj.mchId) ? obj.mchId : 0;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		if(typeof(before) === "function" ){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcCategory/CustTree?customId=' + mchId,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success:function(result){
				if(result.statusCode === 200){
					if(result.data.ResultCode === "0"){
						if(typeof(success) === "function" ){
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function" ){
					fail(result);
				}
			},
			fail:function(errors){
				if( typeof(fail) === "function" ){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		})
	},
	// ==============================================================================================================
}