	/** 
	 * @param {Object} obj 
	 * @param {Object} data 
	 * @property  {Function} before  null
	 * @property {Function} success null
	 * 	@value {Object} data 
	 * @property {Function} fail null
	 * 	@value {Object} errors 
	 * @property {Function} completeFnull
	 */
	getAllList: function(obj) {
		// 
		let data;			// 
		let before;			// 
		let success;		// 
		let fail;			// 
		let complete;		// 
		if (Boolean(obj)) {
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		var params = publicF.urlEncode(data);
		if (typeof(before) === "function") {
			before();
		}
		uni.request({
			url: web.website + '/api/SvcMchStaff/Get?' + params,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success: function(result) {
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "0") {
						if (typeof(success) === "function") {
							success(result.data.Data , result.data.resultMsg);
						}
						return;
					}
				}
				if (typeof(fail) === "function") {
					fail(result);
				}
			},
			fail: function(errors) {
				if (typeof(fail) === "function") {
					fail(errors);
				}
			},
			complete: function() {
				if (typeof(complete) === "function") {
					complete();
				}
			}
		});
	},
