/**
 * 
 * @author 
 * @description 
 * @property {Function} getCurrentInfo 
 * @property {Function} getInfogetInfo id
 * @property {Function} bindPhoneInfo 
 * @property {Function} unbindPhoneInfo 
 * @event {Function(obj)}  updateInfo 
 * 
 *@property {Function} getCurrentIsAdminBoolean  
 *@property {Function} getIsAdminBoolean  
 */
import web from '@/common/variable/website.js';		// 
import login from '@/common/storage/loginstorage.js';	// 
import organ from '@/common/storage/organstorage.js';	// 
import user from '@/common/storage/userstorage.js';		// 

module.exports = {
	/** 
	 * @param {Object} obj
	 * 	@property {Function} before 
	 * 	@property {Function} success 
	 * 	@property {Function} fail 
	 * 	@property {Function} complete 
	 */
	getCurrentInfo: function(obj) {
		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcUser/Info/' + user.getUserId(),
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success: function(result) {
				// 
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "0") {
						if(typeof(success) === "function"){
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function" ){
					fail(result);
				}
			},
			fail:function(errors){
				if(typeof(fail) === "function" ){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		});
	},
	/** Id
	 * @param {Object} obj
	 * 	@property {Int} id Id
	 * 	@property {Function} before 
	 * 	@property {Function} success 
	 * 	@property {Function} fail 
	 * 	@property {Function} complete 
	 */
	getInfo: function(obj) {
		// 
		let id;				// Id
		let before;		// 
		let  success;		// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			id = obj.id;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		
		// 
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcUser/Info/' + id ,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success: function(result) {
				// 
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "0") {
						if(typeof(success) === "function"){
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function" ){
					fail(result);
				}
			},
			fail:function(errors){
				if(typeof(fail) === "function" ){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		});
	},
	/** 
	 * @param {Object} obj
	 * 	@property {Object} data 
	 * 	@property {Function} before 
	 * 	@property {Function} success 
	 * 	@property {Function} fail 
	 * 	@property {Function} complete 
	 */
	bindPhoneInfo : function(obj) {
		// 
		let data;			// 
		let before;			// 
		let success;		// 
		let fail;			// 
		let complete;		// 
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcUser/Bind',
			method: 'POST',
			data: data,
			success: function(result) {
				if(result.statusCode == 200){
					if (result.data.ResultCode == "0") {
						if(typeof(success) === "function"){
							success(result.data.Data , result.data.ServerTime ,result.data.ResultMsg)
						}
						return;
					}
				}
				if(typeof(fail) === "function" ){
					fail(result);
				}
			},
			fail: function(errors) {
				if(typeof(fail) === "function"){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		})
	},
	/** ()
	 * @param {Object} obj
	 */
	unbindPhoneInfo : function(obj){
	},
	/** 
	 * @param {Object} obj
	 * @property {Object} data 
	 * @property {Function} before 
	 * @property {Function} success 
	 * @property {Function} fail 
	 * @property {Function} complete 
	 */
	updateInfo: function(obj) {
		// 
		let data;		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcUser/Update',
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'POST',
			data: data,
			success: function(result) {
				if (result.statusCode == 200) {
					// Session
					if (result.data.ResultCode == "0" ) {
						if(typeof(success) === "function"){
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function"){
					fail(result);
				}
			},
			fail: function(errors) {
				if(typeof(errors) === "function"){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		});
	},
	// ===================================================  ===================================================================
	/** 
	 * @param {Object} obj
	 * 	@property {Function} before 
	 * 	@property {Function} success 
	 * 	@property {Function} fail 
	 * 	@property {Function} complete 
	 */
	getCurrentIsAdminBoolean : function(obj){
		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		
		// 
		if(typeof(before) === "function"){
			before();
		}
		let UserId = user.getUserId();
		let MchId = organ.getIndustryId();
		if(!Boolean(UserId) || !Boolean(MchId)){
			if(typeof(success) === "function"){
				success(false , null);	
			}
			return;
		}
		uni.request({
			url: web.website + '/api/Share/CheckAdmin?userId=' + UserId + '&mchId=' + MchId,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success: function(result) {
				// 
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "0") {
						if(typeof(success) === "function"){
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function" ){
					fail(result);
				}
			},
			fail:function(errors){
				if(typeof(fail) === "function" ){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		});
	},
	/** 
	 * @param {Object} obj
	 * 	@property {Number} mchId id
	 * 	@property {Function} before 
	 * 	@property {Function} success 
	 * 	@property {Function} fail 
	 * 	@property {Function} complete 
	 */
	getIsAdminBoolean : function(obj){
		// 
		let mchId;		// id
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			mchId = Boolean(obj.mchId) ? obj.mchId :  organ.getIndustryId();
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		
		// 
		if(typeof(before) === "function"){
			before();
		}
		var UserId = user.getUserId();
		if(!Boolean(UserId) || !Boolean(mchId)){
			if(typeof(success) === "function"){
				success(false , null);	
			}
			return;
		}
		uni.request({
			url: web.website + '/api/Share/CheckAdmin?userId=' + UserId + '&mchId=' + mchId,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success: function(result) {
				// 
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "0") {
						if(typeof(success) === "function"){
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function" ){
					fail(result);
				}
			},
			fail:function(errors){
				if(typeof(fail) === "function" ){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		});
	},
	// ================================================================================================================================
}