/**
 * 
 * @autho 
 @description  loginNameMobileloginName
 */
import globalf from '@/common/function/globalfuc.js';	// 
module.exports ={
	/** 
	 * @param {String} Mobile	
	 * @param {DateTime} ServerTime 
	 * @param {String} Token	Token
	 * @param {String} TokenExp	Tokens
	 * @param {Number} UserId	UserId
	 * @param {String}   OpenId	OpenId
	 */
	setloginMessage: function(Mobile , ServerTime , Token , TokenExp ,UserId , OpenId = null){
		try{
			uni.setStorageSync("login:loginName", Mobile);		// 
		}catch(e){}
		try{
			uni.setStorageSync("login:serverTime",Date(ServerTime));	 // 
		}catch(e){}
		try{
			uni.setStorageSync("login:token", Token);			// Token
		}catch(e){}
		try{
			uni.setStorageSync("login:tokenExp", TokenExp);		// Token
		}catch(e){}
		try{
			uni.setStorageSync("login:userId", UserId);			// UserId
		}catch(e){}
		try{
			uni.setStorageSync("login:openId", OpenId);			// 
		}catch(e){}
	},
	/**
	 * 
	 */
	getLoginName(){
		try{
			return uni.getStorageSync("login:loginName");
		}catch(e){
			return null;
		}
	},
	/**
	 * 
	 */
	getMobile(){
		try{
			return uni.getStorageSync("login:mobile");
		}catch(e){
			return null;
		}
	},
	/** Token
	 * @param {String} token
	 */
	setToken : function(token){
		uni.setStorageSync("login:token", token);			// Token
	},
	/** Token
	 * @return {String} Token
	 */
	getToken: function(){
		try{
			return uni.getStorageSync("login:token");
		}catch(e){
			return null;
		}
	},
	/**
	 * UserId
	 */
	getUserId(){
		try{
			return uni.getStorageSync("login:userId");
		}catch(e){
			return 0;
		}
	},
	/** openId
	 * @param {Object} OpenId openId
	 */
	setOpenId : function(OpenId){
		uni.setStorageSync("login:openId",OpenId);
	},
	/**
	 * openId
	 */
	getOpenId : function(){
		try{
			return uni.getStorageSync("login:openId");
		}catch(e){
			return null;
		}
	},
	/** 
	 * @param {Object} mchLength 
	 */
	setMchLength : function(mchLength){
		uni.setStorageSync("login:mchLength",mchLength);
	},
	/**
	 * 
	 */
	getMchLength : function(){
		try{
			return uni.getStorageSync("login:mchLength");
		}catch(e){
			return 0;
		}
	},
	/**
	 * true
	 */
	setSkipTrue : function(){
		uni.setStorageSync("login:skip" , true);
	},
	/**
	 * 
	 */
	getSkip : function(){
		try{
			return uni.getStorageSync("login:skip");
		}catch(e){
			return false;
		}
	},
	/**
	 * 
	 */
	clearloginMessage: function(){
		// 
		try{
			globalf.clearSingle("login:loginName" , "login");	// 
			globalf.clearSingle("login:mobile" , "login");	// 
			globalf.clearSingle("login:serverTime" , "login");	// 
			globalf.clearSingle("login:token" , "login");	// Token
			globalf.clearSingle("login:tokenExp" , "login");	// Tokens
			globalf.clearSingle("login:userId" , "login");	// UserId
			globalf.clearSingle("login:openId" , "login");	// openId
			globalf.clearSingle("login:mchLength" , "login");	// 
			globalf.clearSingle("login:skip" , "login");	// /
		}catch(e){
			console.error("login=>",e);
		}
	},
	/**
	 * Token
	 * 
	 */
	isLogin(){
		try{
			// 
			if( !Boolean(uni.getStorageSync("login:loginName")) && !Boolean(uni.getStorageSync("login:serverTime")) && !Boolean(uni.getStorageSync("login:tokenExp"))){
				return false;
			}
			// 
			var loginName = uni.getStorageSync("login:loginName");
			var serverTime = new Date(uni.getStorageSync("login:serverTime"))  ;
			var tokenExp = uni.getStorageSync("login:tokenExp");
			var lastTime = new Date(uni.getStorageSync("login:serverTime")) ;
			 lastTime.setSeconds(lastTime.getMinutes() + tokenExp)  ;		// Tokens
			return Boolean(loginName) &&  (new Date().setSeconds( new Date().getSeconds() + 100 ) >= serverTime ) && new Date() <= lastTime;
		}catch(e){
			console.log("isLogin=>" ,e);
			return false;
		}
	},
	
}