/**
 * 
 * @author 
 * @description 
 * @property {Functin} toSearch	SN 
 * @property {Function} snScan	SN
 * @property {Function} toType   
 */
import lang from '@/common/variable/lang.js'; // 
import zhCN from '../../utils/lang/zh-Cn.js';		// 
var mylang ;
// 
switch (lang.locale) {
	case 'zh-CN':
		mylang = zhCN;
		break;
	default:
		mylang = zhCN;
		break;
}

import deviceR from '@/common/request/device.js';					// 
import repairOrderR from '@/common/request/repairorder.js';			// 
var _self = module.exports = {
	/** SN
	 * @param {Object} obj
	 */
	toSearch : function(obj){
		// 
		uni.navigateTo({
			url:'/pages_repair/search?searchKey=' + obj.value
		});
	},
	/** SN
	 * @param {Object} e
	 */
	snScan: function(e){
		uni.scanCode({
			success:function(re){
				_self.repairNavigateTo(re.result);
			},
			fail:function(errors){
				// 
				uni.showModal({
					title:'',
					content:'',
					success:function(result){
						if(result.confirm){
							uni.navigateTo({
								url:'/pages_repair/search?'
							});
						}
					}
				});
				
			}
		});
	},
	/** SN
	 * @param {Object} DeviceSn SN
	 */
	repairNavigateTo : function(DeviceSn){
		let data = {sn : DeviceSn};
		repairOrderR.getInfoByData({
			data : data,
			success : function(data1){
				if(Boolean(data1)){
					// 
					uni.navigateTo({
						url :'/pages_repair-order/customer/detail?OrderId=' + data1.OrderId
					});
				}else{
					// 
					deviceR.getInfoByData({
						data : data,
						success : function(data2){
							if(Boolean(data2)){
								// SN
								uni.navigateTo({
									url:'/pages_repair/repairsn?DeviceSn=' + DeviceSn
								});
							}else{
								uni.showModal({
									title:'SN',
									content:'',
									success:function(result){
										if(result.confirm){
											uni.navigateTo({
												url:'/pages_repair/search?searchKey='+ DeviceSn
											});
										}
									}
								});
							}
						},
						fail : function(){
							uni.showToast({
								title: mylang.basic.requestFailMessage,
								icon:'none'
							});
						}
					});
				}
			},
			fail: function(){
				console.log("");
				uni.showToast({
					title: mylang.basic.requestFailMessage,
					icon:'none'
				});
			}
		});
	},
	/** 
	 * @param {Object} e
	 */
	toType: function(e){
		uni.navigateTo({
			url:'/pages_repair/repaircate'
		});
	},
}