/**
 * 
 * @property {Function} getCount 
 * @property {Function} getTypeList  
 * @property {Function} messageList  
 * @property {Function} updateReadInfo  
 * 
 * 
 */
import publicF from '@/common/function/publicfunc.js';		// 
import web from '@/common/variable/website.js';				// 
import login from '@/common/storage/loginstorage.js';		// 
import user from '@/common/storage/userstorage.js';			// 
module.exports = {
	/** 
	 * @param {Object} obj
	 * @property {Function} before 
	 * @property {Function} success 
	 * @property {Function} fail 
	 * @property {Function} complete 
	 */
	getCount: function(obj){
		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcMsg/GetMsgCount?userId=' + user.getUserId() + '&isRead=false',
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success: function(result) {
				if (result.statusCode == 200) {
					if(typeof(success) === "function" ){
						success(result.data.Data , result.data.ResultMsg);
					}
					return;
				} 
				if(typeof(fail) === "function"){
					fail(result);
				}
			},
			fail: function(errors) {
				if(typeof(fail) === "function"){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function" ){
					complete();
				}
			}
		});
	},
	/** 
	 * @param {Object} obj
	 * @property {Object} data  
	 * @property {Function} before 
	 * @property {Function} success 
	 * @property {Function} fail 
	 * @property {Function} complete 
	 */
	getTypeList : function(obj){
		// 
		let data;		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(typeof(before) === "function"){
			before();
		}
		var param = publicF.urlEncode(data);
		uni.request({
			url: web.website + '/api/SvcMsgType/List?' + param,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success:function(result){
				if (result.statusCode == 200) {
					if(typeof(success) === "function"){
						success(result.data.Data ,result.data.ResultMsg);
					}
					return;
				}
				if(typeof(fail) === "function"){
					fail(result);
				}
			},
			fail:function(errors){
				if(typeof(fail) === "function"){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		})
	},
	/**
	 * @param {Object} obj
	 * @property {Object} data 
	 * @property {Function} before 
	 * @property {Function} success 
	 * @property {Function} fail 
	 * @property {Function} complete 
	 */
	getList(obj) {
		// 
		let data;		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		let params = publicF.urlEncode(data);		// 
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcMsg/GetMessages?' + params,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success:function(result){
				if(result.statusCode == 200){
					if (result.data.ResultCode == "0") {
						if(typeof(success) === "function"){
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function"){
					fail(result);
				}
			},
			fail:function(errors){
				if(typeof(fail) === "function"){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		});
	},
	/** 
	 * @param {Object} obj
	 * @property {Object} data 
	 * @property {Function} before 
	 * @property {Function} success 
	 * @property {Function} fail 
	 * @property {Function} complete 
	 */
	updateReadInfo(obj){
		// 
		let data;		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		let params = publicF.urlEncode(data);		// 
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcMsg/UpdateRead?' + params,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'POST',
			success:function(result){
				if(result.statusCode == 200){
					if (result.data.ResultCode == "0" ) {
						if(typeof(success) === "function"){
							success(result.data.Data , result.data.ResultMsg);
						}
						return;
					}
				}
				if(typeof(fail) === "function"){
					fail(result);
				}
			},
			fail:function(errors){
				if(typeof(fail) === "function"){
					fail(errors);
				}
			},
			complete:function(){
				if(typeof(complete) === "function"){
					complete();
				}
			}
		});
	}
	// =========================================================================================================================
}