module.exports = {
	/** list "value"
	 * @param {Object} list 
	 * @param {Object} value value
	 * @description 
	 *  
	 *   filterbyvalue 
	 */
	filterbyvalue_list : function(list ,value){
		if(list != null){
			for(var index = 0 ; index < list.length ; index ++){
				var item = this.filterbyvalue(list[index] , value);
				if(item == null){
					list.splice(index,1);			// 
					break;
				}
			}
		}
		return list;
	},
	/** obj obj"value"
	 * @param {Object} obj objdata-com
	 * @param {Object} value value
	 * @description 
	 *  
	 *  filterbyvalue_list
	 */
	filterbyvalue : function (obj , value){
		if(!obj){return null;}
		if(obj.value == value){ 
			// 
			obj = null;
		}else if(obj.children != null){
			if(obj.children.length > 0){
				this.filterbyvalue_list(obj.children , value);
			}
		}
		return obj;
	}
}