/**
 * 
 * @property {type} getTreesFormat (datacom)
 */
import web from '@/common/variable/website.js';		// 
import login from '@/common/storage/loginstorage.js';	// 
import changeobjF from '@/common/function/changeobjectecordname.js';	// 

module.exports = {
	/** (datacom)
	 * @param {Object} obj
	 * @property {Function} before 
	 * @property {Function} success 
	 * @property {Function} fail 
	 * @property {Function} complete 
	 */
	getTreesFormat : function (obj) {
		// 
		let before ;	// 
		let success;	// 
		let fail ;		// 
		let complete ;	// 
		if(Boolean(obj)){
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/Share/GetIndustry',
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'GET',
			success: function(result) {
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "0") {
						if(typeof(success) === "function"){
							success(changeobjF.change(result.data.Data,"IndustryId","Name","Children") , result.data.ResultMsg);
						}
						return;
					}  
				} 
				if(typeof(fail) === "function"){
					fail(result);
				}
			},
			fail: function(errors) {
				if(typeof(fail) === "function"){
					fail(errors);
				}
			},
			complete: function() {
				if(typeof(complete) == "function"){
					complete();
				}
			}
		});
	},
	// =================================================================================================================================
}