/**
 * 
 * @author 
 * @property {Function} login 
 * @property {Function} updatePwd 
 * 
 */
import web from '@/common/variable/website.js';			// 
import login from '@/common/storage/loginstorage.js';	// 
import publicF from '@/common/function/publicfunc.js';	// 
module.exports = {
	/** 
	 * @param {Object} obj 
	 * @property {Object} 
	 * @property {Fuction} before  
	 * @property {Fuction} success  
	 * @property {Fuction} nobind  
	 * @property {Fuction} fail  
	 * @property {Fuction} complete  
	 */
	login : function(obj){
		// 
		let data;		// 
		let before;		// 
		let success;	// 
		let nobind ;	// 
		let fail ;		// 
		let complete ;	// ()
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			nobind = obj.nobind ;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		uni.showLoading({
			title: ''
		});
		if(typeof(before) === "function"){
			before;
		}
		// 
		uni.request({
			url: web.website + '/api/SvcUser/Login',
			method: 'POST',
			data: data,
			success: function(result) {
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "601") {
						// 
						if(typeof(nobind) === "function"){
							nobind(result.data.Data , result.data.ResultMsg);
						}
						return;
					} else{
						// 
						if(typeof(success) === "function"){
							success(result.data.Data ,result.data.ServerTime ,result.data.ResultMsg);
						}
					}
					return;
				} 
				if(typeof(fail) === "function"){
					fail(result);
				}
			},
			fail: function(errors) {
				if(typeof(fail) === "function"){
					fail(errors);
				}
			},
			complete: function() {
				if(typeof(complete) === "function"){
					complete();
				}
			}
		});
	},
	/** 
	 * @param {Object} obj
	 * @property {Object} data 	
	 * @property {Function} before 
	 * @property {Function} success 
	 * @property {Function} fail 
	 * @property {Function} complete 
	 */
	updatePwd(obj) {
		// 
		let data;		// 
		let before;		// 
		let success;	// 
		let fail;		// 
		let complete;	// 
		if(Boolean(obj)){
			data = obj.data;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		let params = publicF.urlEncode(data);
		if(typeof(before) === "function"){
			before();
		}
		uni.request({
			url: web.website + '/api/SvcUser/UpdatePwd?' + params,
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			method: 'POST',
			success: function(result) {
				uni.hideLoading();
				if (result.statusCode == 200) {
					if (result.data.ResultCode == "0" ) {
						if(typeof(success) === "function"){
							success(result.data , result.data.ResultMsg);
						}
						return;
					}
				} 
				if(typeof(fail) === "function"){
					fail(result);
				}
			},
			fail: function(errors) {
				if(typeof(fail) === "function"){
					fail(errors);
				}
			},
			complete: function() {
				if(typeof(complete)){
					complete();
				}
			}
		});
	}
	// ================================================================================================================================
}