/**
 * 
 * @author 
 * @description  
 * @property {Function} imgUpload  
 * @property {Function} imgListUpload 
 * @event {Function()} imgListIncreUpload 
 */

import web from '@/common/variable/website.js'; // 
import login from '@/common/storage/loginstorage.js'; // 

module.exports = {
	/** 
	 * @param {Object} obj 
	 * @property {String} imgUrl 
	 * @property  {String} LoadingText ""
	 * @property {Function} before 
	 * @property {Function} success null
	 * @property {Function} fail (null)
	 * @property {Function} complete null
	 * @param {Boolean}  showTitle loadingtrue
	 */
	imgUpload: function(obj , showTitle = true) {
		//
		let imgUrl;
		let LoadingText = ''
		let before;		// 
		let success;
		let fail;
		let complete;
		if (Boolean(obj)) {
			imgUrl = obj.imgUrl;
			LoadingText = Boolean(obj.LoadingText) ? obj.LoadingText : LoadingText;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(showTitle){
			uni.showLoading({
				title: LoadingText
			});
		}
		if(typeof(before) == "function"){
			before();
		}
		uni.uploadFile({
			url: web.website + '/api/Share/Upload',
			filePath: imgUrl,
			name: 'files',
			header: {
				Authorization: 'Bearer ' + login.getToken()
			},
			success: function(result) {
				if(showTitle){
					uni.hideLoading();
				}
				if (result.statusCode == 200) {
					var data = JSON.parse(result.data);
					if (typeof(success) == "function") {
						success(data.Data , result.data.ResultMsg );
					}
					return;
				}
				if (typeof(fail) === "function") {
					fail(result , result.data.ResultMsg);
				}
			},
			fail: function(errors) {
				if(showTitle){
					uni.hideLoading();
				}
				if (typeof(fail) === "function") {
					fail(errors , null);
				}
			},
			complete: function() {
				if (typeof(complete) === "function") {
					complete();
				}
			}
		});
	},
	/** ()
	 * @param {Object} obj
	 * @property {Number} index 
	 * @property {Array} imgs 
	 * @property {Number} size imgs
	 * @property {Array} imgsResult []
	 * @property {Array} before 
	 * @property {Function} success index
	 * @property {Function} fail index
	 * @property {Function} complete index
	 * @param {Boolean} showTitle titletrue
	 */
	imgListUpload: function(obj , showTitle = true) {
		var _self = this;
		// 
		let index = 0;			// 
		let imgs = [];			// 
		let size = 0;			// 
		let imgsResult = [];	// 
		let before;			// 
		let success;		// index
		let fail;		// index
		let complete;	// 
		if (Boolean(obj)) {
			index = Boolean(obj.index) ? obj.index : 0;
			imgs = Boolean(obj.imgs) ? obj.imgs : imgs;
			size = Boolean(obj.size) ? obj.size : imgs.length;
			imgsResult = Boolean(obj.imgsResult) ? obj.imgsResult : imgsResult;
			before = obj.before;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(typeof(before) == "function" && index == 0){
			before();
		}
		if (index < size) {
			this.imgUpload({
				imgUrl: imgs[index],
				LoadingText: "" + (index + 1) + "",
				success: function(data , message) {
					imgsResult.push(data);
					if(index === (size - 1)){
						if(typeof(success) == "function"){
							success(imgsResult , message);
						}
					}
				},
				fail: function(errors) {
					imgsResult.push(imgs[index]);
					if(index === (size - 1)){
						if(typeof(fail) == "function"){
							fail(imgsResult , errors);
						}
					}
				},
				complete: function() {
					// 
					if (index === (size - 1)) {
						// 
						if(typeof(complete) === "function"){
							complete();
						}
					} else {
						// 
						_self.imgListUpload({
							index : index + 1,
							imgs,
							size ,
							imgsResult,
							success,
							fail,
							complete
						});
					}
				}
			} , showTitle);
		}
	},
	/** 
	 * @param {Object} obj
	 * @property {Number} index 
	 * @property {Array} imgs 
	 * @property {Number} size imgs
	 * @property {Array} imgsResult []
	 * @property {Function} before 
	 * @property {Function} success index
	 * @property {Function} fail index
	 * @property {Function} complete index
	 * @param {Boolean} showTitle loadingtrue
	 */
	imgListIncreUpload: function(obj ,showTitle = true) {
		var _self = this;
		// 
		let index = 0;			// 
		let beforeImgs =[];		// 
		let imgs = [];			// 
		let size = 0;			// 
		let imgsResult = [];	// 
		let imgsPaths = [];		// 
		let before;				// 
		let itemsuccess;			// 
		let itemfail;			// 
		let success;		// index
		let fail;		// index
		let complete;	// 
		if (Boolean(obj)) {
			index = Boolean(obj.index) ? obj.index : 0;
			beforeImgs = Boolean(obj.beforeImgs) ? obj.beforeImgs : beforeImgs;
			imgs = Boolean(obj.imgs) ? obj.imgs : imgs;
			size = Boolean(obj.size) ? obj.size : imgs.length;
			imgsResult = Boolean(obj.imgsResult) ? obj.imgsResult : imgsResult;
			imgsPaths = Boolean(obj.imgsPaths) ? obj.imgsPaths:imgsPaths;
			before = obj.before;
			itemsuccess = obj.itemsuccess;
			itemfail = obj.itemfail;
			success = obj.success;
			fail = obj.fail;
			complete = obj.complete;
		}
		// 
		if(typeof(before) == "function" && index == 0){
			before();
		}
		if (index < size) {
			let temp = beforeImgs.find(function(item){
				return item.file_url == imgs[index];
				return true;
			});
			if(!Boolean(temp)){
				// 
				this.imgUpload({
					imgUrl: imgs[index],
					LoadingText: "" + (index + 1) + "",
					success: function(data , message) {
						imgsResult.push(data);
						imgsPaths.push(data ? data.file_url : null);
						if(typeof(itemsuccess) == "function"){
							itemsuccess(data , index ,message);
						}						
						if(index === (size - 1)){
							if(typeof(success) === "function"){
								success(imgsResult , imgsPaths , message);
							}
						}
					},
					fail: function(errors) {
						imgsResult.push(imgs[index]);
						imgsPaths.push(imgs[index]);
						if(typeof(itemfail) == "function"){
							itemfail(null , index ,errors);
						}
						if(index === (size - 1)){
							if(typeof(fail) === "function"){
								fail(imgsResult , imgsPaths , errors);
							}
						}
					},
					complete: function() {
						// 
						if (index === (size - 1)) {
							// 
							if(typeof(complete) === "function"){
								complete();
							}
						} else {
							// 
							_self.imgListIncreUpload({
								index : index + 1,
								imgs,
								size ,
								imgsResult,
								imgsPaths,
								before,
								itemsuccess,
								itemfail,
								success,
								fail,
								complete
							},showTitle);
						}
					}
				} , showTitle);
			}else{
				imgsResult.push(temp);
				imgsPaths.push(temp.file_url);
				if(typeof(itemsuccess) == "function"){
					itemsuccess(temp , index ,"");
				}	
				if (index === (size - 1)) {
					// 
					if(typeof(success) === "function"){
						success(imgsResult ,imgsPaths ,"");
					}
					// 
					if(typeof(complete) === "function"){
						complete();
					}
				} else {
					// 
					_self.imgListIncreUpload({
						index : index + 1,
						beforeImgs,
						imgs,
						size ,
						imgsResult,
						imgsPaths,
						before,
						itemsuccess,
						itemfail,
						success,
						fail,
						complete
					} , showTitle);
				}
			}
		}
	},
	// ================================================================================================================================
}
