/**
 * 
 * @description tranformStatus : value
 * 				tranformStatusCNName  
 * 				getStatuClass 
 */
module.exports ={
	/** value
	 * @param {Number} value 
	 * @param {String} orderType 
	 * 	@value customer 
	 *  @value service 
	 * @return {String} 
	 */
	tranformStatus : function(value , orderType="customer"){
		var status = '';		// 
		switch(orderType){
			case 'customer': 
				// 
				switch(value){
					case -1: status = '';break;  // 
					// case 0:	status = 'Created';	break;	// 
					case 1:	status = 'Created,Unprocessed';	break; // 
					case 2: status = 'SvcConfirmed,RepairConfirm,Processing';break; // 
					case 3:	status = 'Uncheck';	break;	// 
					case 4:	status = 'Checked,Finished';break;	// 
					case 5:	status = 'Cancel'; break; //  -Cancel
					default:break;
				}
				break;
			case 'service': 
				switch(value){
					case -1 :status = '';break;	// 
					case 0 :status = 'Unprocessed';break;// 
					case 1 :status = 'SvcConfirmed';break; // 
					case 2 :status = 'RepairConfirm,Processing';break; // 
					case 3 : status = 'Uncheck';break;// 
					case 4 :status = 'Checked,Finished';	break;	// 
					default:break;
				}
				break;
			default:break;
		}
		return status;
	},
	/** 
	 * @param {String} status 
	 * 	@value Created 
	 *  @value Unprocessed 
	 *  @value SvcConfirmed 
	 *  @value RepairConfirm 
	 *  @value Processing 
	 *  @value Uncheck 
	 *  @value Checked 
	 *  @value Finished 
	 *  @value Cancel 
	 * @param {String} orderType 
	 * 	@value customer 
	 *  @value service 
	 * @return {String} 
	 * @description orderType
	 */
	tranformStatusCNName : function(status ,orderType = "customer"){
		var statusCn = '';
		switch(status){
			case 'Created': statusCn='';	break;//  
			case 'Unprocessed':	statusCn=''; break;//  
			case 'SvcConfirmed': statusCn='';	break;	//  
			case 'RepairConfirm':statusCn='';	break;//  
			case 'Processing': 	statusCn='';break;	//  
			case 'Uncheck': statusCn='';break;	//  
			case 'Checked': statusCn='';break;	//  
			case 'Finished':statusCn='';break;	//  
			case 'Cancel':statusCn='';break;	//  
			default:break;
		}
		return statusCn;
	},
	/** 
	 * @param {String} status 
	 * 	@value Created 
	 *  @value Unprocessed 
	 *  @value SvcConfirmed 
	 *  @value RepairConfirm 
	 *  @value Processing 
	 *  @value Uncheck 
	 *  @value Checked 
	 *  @value Finished 
	 *  @value Cancel 
	 * @param {String} orderType 
	 * 	@value customer 
	 *  @value service 
	 * @return {String} 
	 * @description 
	 */
	getStatuClass(status ,orderType = "customer"){
		var bgColor = '';			// 
		switch(orderType){
			case 'customer' : 
				// 
				switch(status){
					case 'Unprocessed': bgColor = 'bg-red';break;
					case 'Processing': bgColor = 'bg-cyan';break;
					case 'Uncheck': bgColor = 'bg-orange';break;
					case 'Finished': bgColor = 'bg-grey' ;break;
					case 'Cancel': bgColor = 'bg-grey';break;
					default: bgColor = 'bg-grey';break;
				}
				break;
			case 'service' : 
				// 
				switch(status){
						case 'Unprocessed': bgColor = 'bg-red';break;
						case 'Processing': bgColor = 'bg-cyan';break;
						case 'Uncheck': bgColor = 'bg-orange';break;
						case 'Finished': bgColor = 'bg-grey' ;break;
						default:bgColor = 'bg-grey';break;
					}
				break;
			default:bgColor = 'bg-grey';break;
		}
		return bgColor;
	},
	/** 
	 * @param {String} status 
	 * 	@value Created 
	 *  @value Unprocessed 
	 *  @value SvcConfirmed 
	 *  @value RepairConfirm 
	 *  @value Processing 
	 *  @value Uncheck 
	 *  @value Checked 
	 *  @value Finished 
	 *  @value Cancel 
	 * @return {Number}  0 ~4
	 * @description 
	 */
	getSteps : function(status ){
		var steps = 0			// 
		switch(status){
			case 'Created': steps = 0;break;		
			case 'Unprocessed': steps = 0;break;
			case 'SvcConfirmed': steps = 1;break;
			case 'RepairConfirm': steps = 2;break;
			case 'Processing': steps = 2;break;
			case 'Uncheck': steps = 3;break;
			case 'Checked': steps = 4;break;
			case 'Finished': steps = 4 ;break;
			case 'Cancel': steps = 1;break;
			default: steps = 0;break;
		}
		return steps;
	}
}